$prime: #565857;
$second: #06d6a0;

body,
html {
    width: 100%;
    height: 100%;
    align-items: center;
    background-color: $second;
    display: flex;
    font-family: 'Roboto';
    font-size: 10px;
    justify-content: center;
    margin: 0;
    padding: 0;

    .wrapper {
        width: 100vw;
        height: 100vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .homebutton {
            display: flex;
            justify-content: center;
            align-items: center;
            width: 20vw;
            height: 10vh;
            font-size: 30pt;
            color: $prime;
            background-color: $second;
            border: .2rem solid $prime;
            border-radius: 10rem;
            opacity: 1;

            &:focus {
                outline: none; }

            &:loginform {
                flex-direction: column;
                justify-content: space-around; } } } }


